<script lang='ts' setup>
import { reactive, ref } from 'vue'

// api
import { GoogleAPiPhotos } from '@/api/endpoints/google'

//  components
import Item from './components/item/index.vue'
import { IPhoto } from '@/api/endpoints/google/types'

// photos inner service
const photosService = reactive({
  items: [] as Array<IPhoto>,

  setItems: function (items: Array<IPhoto>) {
    this.items = items
  }
})

// dialog
const dialogState = ref(false)
let dialogSelectePhoto = ref<string>()

// api
GoogleAPiPhotos().then((data) => {
  Object.keys(data.photos)
  photosService.setItems(data.photos)
}, (err) => {
  console.error(err)
})

// methods
const onChosePhoto = (url) => {
  dialogState.value = true
  dialogSelectePhoto = require(`@/assets/gallery/${url}`)
}
</script>

<template>
  <v-card color="grey-lighten-3">
    <template v-slot:image>
      <v-img gradient="to top right, rgba(19,84,122,.8), rgba(128,208,199,.8)"></v-img>
    </template>

    <div class="gallery-photo-wrapper">
      <v-container max-width="1200px">
        <!-- photos -->
        <v-row>
          <v-col
            v-for="item in photosService.items"
              cols="6"
              sm="2"
              md="2"

            :key="item.id"
          >
            <Item
              :id="item.id"
              :url="item.url"
              @chose-photo="onChosePhoto($event)"
            />
          </v-col>
        </v-row>

      </v-container>

    </div>

  </v-card>
  <!-- PHOTOS -->

  <!-- DIALOG -->
  <v-dialog
    v-model="dialogState"
    max-width="800"
  >
    <v-card>
      <template v-slot:text>
        <!-- dialog controls -->
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            text="Close"
            icon="mdi-window-close"
            variant="text"
            @click="dialogState = false"
          ></v-btn>
        </v-card-actions>

        <!-- dialog content -->
        <div class="dialog-img-wrapper">
          <v-img
            :src="dialogSelectePhoto"
            alt="concretePhotoUrl"
          />
        </div>
      </template>
    </v-card>
  </v-dialog>

</template>
