import { GooglePhotoResponse, IPhoto } from './types'

const files = (lastPhoto): Array<string> => {
  const arr: Array<string> = []
  for (let i = 1; i < lastPhoto; i++) {
    const item = `${i}.jpg`
    arr.push(item)
  }

  return arr
}

const converter = (files: Array<string>): Array<IPhoto> => {
  return files.map((item) => {
    const paht = require(`@/assets/gallery/${item}`)
    return {
      id: item,
      url: paht
    }
  })
}

export const GoogleAPiPhotos = async (): Promise<{
  photos: Array<IPhoto>
}> => {
  try {
    const googlePhotoResponse: {photos: Array<IPhoto>} = {
      photos: converter(files(20))
    }
    return Promise.resolve(googlePhotoResponse)
  } catch (err) {
    return Promise.reject(new Error(err))
  }
}
